.project {
    display: flex;
    flex-grow: 10;
    overflow: hidden;
    flex-direction: column;
    background: none;
    justify-content: space-between;
    width: auto;
    position: relative;
    align-items: right;
    box-sizing: border-box;
    .projects-path {
        display: flex;
        flex-direction: row;
        align-items: center;
        background: #CEDDEF;
        width: 100%;
        height: fit-content;
        padding: 6px 4px 6px;
        margin: 0;
        gap: 8px;
        font-weight: bold;
        position: sticky;
        top: 0;
        z-index: 100;
        right: 0;
        .slash {
            opacity: 1;
            font-size: 20px;
            padding: 0;
            margin: 0;
            height: fit-content;
            &:last-child {
                opacity: 0;
            }
        }
        button {
            padding: 0;
            margin: 0;
            border-radius: 0;
            background: none;
            color: #070D59;
            height: fit-content;
            font-size: 18px;
            &:hover {
                color: #5893D4;
                cursor: pointer;
                background: none;
            }
        }
    }
    .project-container {
        padding: 0;
        margin: 0;
        .project-header {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            height: fit-content;
            gap: 0;
            padding: 10px;
            .project-title {
                h1 {
                    font-size: 26px;
                    color: #1F3C88;
                    margin: 0;
                }
            }
            .exit-button {
                display: flex;
                background: #CEDDEF;
                width: fit-content;
                height: fit-content;
                svg {
                    fill: #8F384D !important;
                    &:hover {
                        fill: #792d3a !important;
                        cursor: pointer;
                    }
                }
                &:hover {
                    background: #5893D4;
                }
            }
            .buttons {
                display: flex;
                align-items: center;
                .edit-button {
                    background: none;
                    padding: 0;
                    margin: 0;
                    svg {
                        fill: #5893D4 !important;
                        &:hover {
                            fill: #1F3C88 !important;
                            cursor: pointer;
                        }
                    }
                    width: fit-content;
                    height: fit-content;
                }
                .protocol-link {
                    width: fit-content;
                    height: fit-content;
                    padding: 10px;
                    padding-bottom: 6px;
                    padding-top: 6px;
                    border-radius: 4px;
                    text-decoration: none;
                    color: #1F3C88;
                    background: #FFDE7D;
                    font-weight: bold;
                    margin: 8px;
                    &:hover {
                        color: #070D59;
                        background: #fdcf45;
                        cursor: pointer;
                    }
                }
            }
        }
        .content {
            display: flex;
            flex-direction: row;
        }
        
        h2 {
            color: #5893D4;
            padding: 0;
            margin: 0;
            font-family: inherit;
            font-size: 20px;
            font-weight: normal;
            width: fit-content;
        }
        .sub-projects-container {
            width: 380px;
            max-height: calc(100vh - 280px);
            overflow-x: hidden;
            overflow-y: auto;
            h1 {
                width: 100%;
                text-align: center;
            }
            .sub-projects {
                display: flex;
                flex-direction: column;
            }
        }
    }
}

